import axios from 'axios';
import React from 'react'
import { useNavigate } from 'react-router-dom';
import { useEffect } from 'react';

axios.defaults.withCredentials = true;

function Success(){
    const navigate = useNavigate();
    
    useEffect(() => {
        axios.post('https://port-0-node-react-2-1luhct24lcosbqtn.gksl2.cloudtype.app/api/users/auth')
        .then(response => {
            console.log(response.data)
            if (response.data.isAuth === false)
                navigate('/login');
        });
    })
    
    return (
        <>
        <div><h1>  !</h1></div>
        <button className='keyboard' style={{
            width: '220px'
          }} onClick={(e)=>{
            e.preventDefault();
            axios.post('https://port-0-node-react-2-1luhct24lcosbqtn.gksl2.cloudtype.app/api/users/logout')
            .then(response => {
                if (response.data.success){
                    navigate('/');
                }
                else{
                    alert(' .');
                }
            })
          }}>
            logout
          </button>
        </>
      
    )
}

export default Success