import React from 'react'
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
axios.defaults.withCredentials = true;

function RegisterPage() {
  const navigate = useNavigate();
  const [Email, setEmail] = useState("");
  const [Name, setName] = useState("");
  const [Password, setPassword] = useState("");
  const [Password2, setPassword2] = useState("");

  return (
    <div style={{
      display: 'flex', justifyContent: 'center', alignItems: 'center',
      width: '100%', height: '100vh' 
    }}>
      <form style={{display: 'flex', flexDirection: 'column'}} onSubmit={(e)=>{
        e.preventDefault();
        if (Password !== Password2)
          alert("confirm password password  .");
        else{

        let body = {
          name: Name,
          email: Email,
          password: Password
        }

        axios.post('https://port-0-node-react-2-1luhct24lcosbqtn.gksl2.cloudtype.app/api/users/register', body)
        .then(response => {
          if (response.data.success === true){
            console.log(" ");
            navigate('/');
          }
          else{
            console.log(" ");
            alert(" ,  .");
          }
        })
        .catch(error => console.log(error));
      }}}>

          <input className="keyboard" style={{
            paddingLeft:'10px',
            paddingRight:'10px'
          }} type='email' value={Email} placeholder='email' onChange={(e)=>{
            setEmail(e.target.value);
          }}/>

          <br/>
          <input className="keyboard" style={{
            paddingLeft:'10px',
            paddingRight:'10px'
          }} type='text' value={Name} placeholder='name' onChange={(e)=>{
            setName(e.target.value);
          }}/>

          <br/>
          <input className="keyboard" style={{
            paddingLeft:'10px',
            paddingRight:'10px'
          }} type='password' value={Password} placeholder='password' onChange={(e)=>{
            setPassword(e.target.value);
          }}/>

          <br/>

          <input className="keyboard" style={{
            paddingLeft:'10px',
            paddingRight:'10px'
          }} type='password' value={Password2} placeholder='confirm password' onChange={(e)=>{
            setPassword2(e.target.value);
          }}/>

          <br/>

          <button className='keyboard' style={{
            width: '220px'
          }}>
            register
          </button>
          <br/>

          <button className='keyboard' style={{
            width: '220px'
          }} onClick={(e)=>{
            e.preventDefault();
            navigate('/login');
          }}>
            back
          </button>

      </form>
    </div>
  )
}

export default RegisterPage