import axios from 'axios';
import React from 'react'
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
axios.defaults.withCredentials = true;

function LoginPage() {
  const navigate = useNavigate();
  const [Email, setEmail] = useState("");
  const [Password, setPassword] = useState("");

  return (
    <div style={{
      display: 'flex', justifyContent: 'center', alignItems: 'center',
      width: '100%', height: '100vh' 
    }}>
      <form style={{display: 'flex', flexDirection: 'column'}} onSubmit={(e)=>{
        e.preventDefault();
        console.log(Email);
        console.log(Password);

        let body = {
          email: Email,
          password: Password
        }

        axios.post('https://port-0-node-react-2-1luhct24lcosbqtn.gksl2.cloudtype.app/api/users/login', body)
        .then(response => {
          if (response.data.loginSuccess === true){
            console.log(" ");
            navigate('/success');
          }
          else{
            console.log(" ");
            alert(response.data.message);
          }
        })
        .catch(error => console.log(error));
      }}>

          <input className="keyboard" style={{
            paddingLeft:'10px',
            paddingRight:'10px'
          }} type='email' value={Email} placeholder='email' onChange={(e)=>{
            setEmail(e.target.value);
          }}/>

          <br/>
          <input className="keyboard" style={{
            paddingLeft:'10px',
            paddingRight:'10px'
          }} type='password' value={Password} placeholder='password' onChange={(e)=>{
            setPassword(e.target.value);
          }}/>

          <br/>

          <button className='keyboard' style={{
            width: '220px'
          }}>
            login
          </button>
          <br/>

          <button className='keyboard' style={{
            width: '220px'
          }} onClick={(e)=>{
            e.preventDefault();
            navigate('/register');
          }}>
            register
          </button>

      </form>
    </div>
  )
}

export default LoginPage